import React, {Component} from 'react';
import {Link} from 'react-router';

class Establishment extends Component {

    render() {

        let id = this.props.establishment.id;
        let url = "/happyhour/" + id;

        let upIcon = <i className="fa fa-thumbs-up" aria-hidden="true"/>;
        let downIcon = <i className="fa fa-thumbs-down" aria-hidden="true"/>;

        let starIcon = <i className="fa fa-star-o" aria-hidden="true"/>;

        if (!this.props.establishment.isLiked)
            upIcon = <i className="fa fa-thumbs-o-up" aria-hidden="true"/>;

        if (!this.props.establishment.isDisliked)
            downIcon = <i className="fa fa-thumbs-o-down" aria-hidden="true"/>;

        if (this.props.establishment.favori)
            starIcon = <i className="fa fa-star favoriIcon" aria-hidden="true"/>;

        return (
            <div className="establishment">
                <div className="establishment-favori">
                    <button onClick={() => this.props.favori(id)}>{starIcon}</button>
                </div>
                <div className="establishment-description">
                    <h3>{this.props.establishment.name}</h3>

                    <div>
                        {this.props.establishment.description}
                    </div>

                    <div>
                        <button >
                            <Link to={url}>Voir l'happy-hour</Link>
                        </button>
                    </div>

                </div>
                <div className="establishmentLikeDislike">
                    <button onClick={() => this.props.like(this.props.establishment, this.props.establishment.isLiked)}>{upIcon}
                    </button>
                    <span>{this.props.establishment.likeCounter}</span>
                    <button onClick={() => this.props.dislike(this.props.establishment, this.props.establishment.isDisliked)}>{downIcon}</button>
                    <span>{this.props.establishment.dislikeCounter}</span>
                </div>

            </div>
        );
    }
}

Establishment.propTypes = {
    state: React.PropTypes.object,
    establishment: React.PropTypes.object,
    favori: React.PropTypes.func,
    like: React.PropTypes.func,
    dislike: React.PropTypes.func
};

export default Establishment;



